import {
	GetModelByWXCode
} from "@/api/customer.js"
const state = { //state
	userInfo: null,
	isLogin: false,
};

const getters = { //state()

}

const mutations = {
	 writeUserInfo(state,user) {
		uni.setStorageSync('isLogin', true);
		uni.setStorageSync('userInfo', user);
		state.isLogin = true;
		state.userInfo = user;
		return user;
	},
	clearUserInfo(state){
		uni.setStorageSync('isLogin', false);
		uni.removeStorageSync('userInfo');
		state.isLogin=false;
		state.userInfo=null;
	}
};

//vuexAPIactionsactions
// Action mutations
// actionscontext
// contextstorecontext.commit(' '),
const actions = {
	getUserInfo({
		state,
		commit
	}) { //mutationscontextstore 
		return new Promise((resolve, reject) => {
			let userInfo = uni.getStorageSync('userInfo');
			if (!userInfo) {
				resolve(null);
			} else {
				if(state.isLogin){
					return state.userInfo;
				}
				GetModelByWXCode(userInfo.wX_MiniOpenId).then(res => {
					if (res.code == 1) {
						commit('writeUserInfo', res.data);
						resolve(state.userInfo);
					}
				}).catch(err => {
					reject(err)
				});
			}

		});
	},
	writeUserInfo(context,userInfo) { 
	   return new Promise((resolve, reject) => {
		   context.commit('writeUserInfo',userInfo);
		   resolve({code:1,state:"ok"});
	   }) 
	},
	clearUserInfo(context) {
	   return new Promise((resolve, reject) => {
		   context.commit('clearUserInfo');
		   resolve({code:1,state:"ok"});
	   }) 
	},
	
};

const user = {
	state: () => ({
		...state
	}),
	mutations: mutations,
	actions: actions,
	getters: getters
}

export default user
